<template>
    <v-container fluid>    
        <div class="container">
            <v-form v-model="isUserFormValid" class="container__form" ref="userForm">
                <h3 class="container__form__title">Personal</h3>            
                <v-row dense>
                    <v-col cols="12" sm="8">
                        <v-text-field
                        v-model.trim="userData.name"
                        :rules="[userRules.required, userRules.minWidth]"
                        label="Name"
                        solo
                        placeholder="Name"
                        min-width="3"
                        @keypress="(event) => handleKeypress('string', event)"
                        />
                    </v-col>
                    <v-col cols="12" sm="4">
                        <v-text-field
                        v-model.trim="userData.id"
                        :rules="[userRules.required]"
                        label="id"
                        solo
                        type="number"
                        placeholder="Id"                        
                        @keypress="(event) => handleKeypress('number', event)"
                        />
                    </v-col>
                </v-row>
                <v-row dense>
                    <v-col cols="12" sm="6">
                        <v-text-field
                        v-model.trim="userData.username"
                        :rules="[userRules.required, userRules.minWidth]"
                        label="username"
                        solo
                        placeholder="Username"
                        />
                    </v-col>
                    <v-col cols="12" sm="6">
                        <v-text-field
                        v-model.trim="userData.email"
                        type="email"
                        :rules="[userRules.required, userRules.email]"
                        label="E-mail"
                        solo
                        placeholder="E-mail"
                        />
                    </v-col>
                </v-row>
                <h3 class="container__form__title">Address</h3>
                <v-row dense>
                    <v-col cols="12" sm="4">
                        <v-text-field
                        v-model.trim="userData.address.street"
                        :rules="[userRules.required, userRules.minWidth]"
                        label="Street"
                        solo
                        placeholder="Street"
                        />
                    </v-col>
                    <v-col cols="12" sm="4">
                        <v-text-field
                        v-model.trim="userData.address.suite"
                        :rules="[userRules.required, userRules.minWidth]"
                        label="Suite"
                        solo
                        placeholder="Suite"
                        />
                    </v-col>
                    <v-col cols="12" sm="4">
                        <v-text-field
                        v-model.trim="userData.address.city"
                        :rules="[userRules.required, userRules.minWidth]"
                        label="City"
                        solo
                        placeholder="City"
                        type="text"
                        @keypress="(event) => handleKeypress('string', event)"
                        />
                    </v-col>
                </v-row>
                <v-row dense>
                    <v-col>
                        <v-text-field
                        v-model.trim="userData.address.geo.lat"
                        :rules="[userRules.required, userRules.minWidth]"
                        label="latitude"
                        solo
                        type="number"
                        placeholder="Latitude"
                        @keypress="(event) => handleKeypress('number', event)"
                        />
                    </v-col>
                    <v-col>
                        <v-text-field
                        v-model.trim="userData.address.geo.lng"
                        :rules="[userRules.required, userRules.minWidth]"
                        label="longitude"
                        solo
                        type="number"
                        placeholder="Longitude"
                        @keypress="(event) => handleKeypress('number', event)"
                        />
                    </v-col>
                </v-row>
                <v-row dense>
                    <v-col cols="12" sm="6">
                        <v-text-field
                        v-model.trim="userData.phone"
                        :rules="[userRules.required, userRules.phoneNumber]"
                        label="Phone"
                        solo
                        type="number"
                        placeholder="Phone"
                        @keypress="(event) => handleKeypress('number', event)"
                        />
                    </v-col>
                    <v-col cols="12" sm="6">
                        <v-text-field
                        v-model.trim="userData.website"
                        :rules="[userRules.required, userRules.website]"
                        label="Website"
                        solo
                        placeholder="Website"
                        />
                    </v-col>
                </v-row>
                <h3 class="container__form__title">Company</h3>
                <v-row dense>
                    <v-col cols="12">
                        <v-text-field
                        v-model.trim="userData.company.name"
                        :rules="[userRules.required, userRules.minWidth]"
                        label="Company Name"
                        solo
                        placeholder="Company Name"
                        @keypress="(event) => handleKeypress('string', event)"
                        />
                    </v-col>
                    <v-col cols="12">
                        <v-text-field
                        v-model.trim="userData.company.catchPhrase"
                        :rules="[userRules.required, userRules.minWidth]"
                        label="Company Catchphrase"
                        solo
                        placeholder="Company Catchphrase"
                        @keypress="(event) => handleKeypress('string', event)"
                        />
                    </v-col>
                    <v-col cols="12">
                        <v-text-field
                        v-model.trim="userData.company.bs"
                        :rules="[userRules.required, userRules.minWidth]"
                        label="Company Bs"
                        solo
                        placeholder="Company BS"
                        @keypress="(event) => handleKeypress('string', event)"
                        />
                    </v-col>
                </v-row>
                <v-btn
                :disabled="!isUserFormValid"
                @click="handleAddUserClick"
                class="container__form__button"
                >
                    Add User
                </v-btn>
            </v-form>            
            <v-snackbar
                v-model="shouldShowSuccessPopup"
                timeout="-1"            
                right
                absolute
                min-width="150"
                text
                class="container__toast"
            >
            <strong>SUCCESS</strong>
            <v-btn 
                text 
                small
                right
                @click="shouldShowSuccessPopup = false"
            >
                <v-icon>
                    mdi-close
                </v-icon>
            </v-btn>
            </v-snackbar>
        </div>
    </v-container>
</template>

<script setup lang="ts">
import axios from 'axios'
import { ref } from 'vue'

interface UserAddressGeo {
    lat: string,
    lng: string
}

interface UserAddress {
    street: string,
    suite: string,
    city: string,
    zipcode : string,
    geo : UserAddressGeo
}

interface UserCompany {
    name: string,
    catchPhrase : string,
    bs: string
}

interface User {
    id: number | null,
    name: string,
    username: string,
    email: string,
    address : UserAddress,
    phone: string,
    website: string,
    company: UserCompany
}

const isUserFormValid = ref<(boolean)>(false)
const userForm = ref<HTMLFormElement | null>(null)
const shouldShowSuccessPopup = ref<boolean>(false)
const SUCCESS_POPUP_TIMEOUT_IN_MS = 3000
const numInputInvalidKeys = ['-', '+', '.', 'e', 'E']
const initialUserData = {
    id: null,
    name: '',
    username: '',
    email: '',
    address: {
        street: '',
        suite: '',
        city: '',
        zipcode: '',
        geo: {
            lat: '',
            lng: '',
        }
    },
    phone: '',
    website: '',
    company: {
        name: '',
        catchPhrase: '',
        bs: '',
    }
}
const userData = ref<User>(initialUserData)

const userRules = ref({
    required: (value: string | number) => !!value || 'Required',
    email: (value: string) => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        
        return pattern.test(value) || 'Invalid e-mail'
    },
    minWidth: (value:string | number) => String(value).length >= 3 || 'Minimum three Characters',
    website: (value: string)  => /^www\.[a-zA-Z0-9-]+\.[a-zA-Z]{2,}$/.test(value) || 'enter valid website',
    phoneNumber: (value: number) => {
        const phoneNumberPattern = /^[6-9]\d{9}$/

        return phoneNumberPattern.test(String(value)) || 'Invalid Phone Number'
    },
})

const handleKeypress = (value:string, event:KeyboardEvent) => {
    const enteredValue = event.key
    if(value === 'number') {
        if(numInputInvalidKeys.includes(enteredValue) || String(userData.value.id).length > 4) event.preventDefault()
    }
    if(value === 'string') {
        if(!(/[a-zA-Z]/.test(enteredValue))) event.preventDefault()
    }
}

const handleAddUserClick = async () => {
    await axios({
        method: 'POST',
        url: 'https://jsonplaceholder.typicode.com/users',
        data: userData.value
    })
    shouldShowSuccessPopup.value = true
    const modalTimeout = setTimeout(() => {
        shouldShowSuccessPopup.value = false
        clearTimeout(modalTimeout)
    }, SUCCESS_POPUP_TIMEOUT_IN_MS)
    userForm.value?.reset()
}
</script>

