    display: flex;
    align-items: center;
    justify-content: center;

    &__form {
        box-shadow: 1px 2px 7px rgba(0,0,0,0.4);
        border-radius: 10px;
        padding: 20px;

        &__title {
            text-align: left;
            margin-bottom: 10px;
        }

        &__button:disabled {
            pointer-events: all;
            cursor: not-allowed !important;
        }
    }

    &__toast {
        .v-snack__content {
            display: flex;
            width: 100%;
            justify-content: space-between;
        }
    }
}
</style>